html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: "RM Typerighter";
  src: url("RM-Typerighter.woff") format("woff");
  font-display: block;
}

.font-rm-typerighter-regular {
  font-family: "RM Typerighter";
  font-size: 24pt;
}

.vertical {
  transform: rotate(90deg);
  translate: -110px;
}

